/* small spinner used next to flask */
.spinner-small {
  width: 16px;
  height: 16px;
  border: 2px solid #e6e6e6;
  border-top: 2px solid #111;
  border-radius: 50%;
  animation: spin 0.9s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
